/* These need to go in the base css */
.gray-bg {
  background: var(--grey-background, #eee);
}

.sidebar-container {
  background: var(--background-primary-light, #fafafa);
  box-shadow: -4px 4px 4px 0px rgba(0, 0, 0, 0.25);
  display: none;
  font-family: "Noto Sans";
  height: 90vh;
  line-height: normal;
  overflow-y: auto;
  padding: 3.5rem 1rem;
  position: absolute;
  right: 0px;
  text-align: start;
  top: 88px;
  transition: all 0.3s ease;
  width: 15rem;
}

.transparent-bg {
  background: transparent;
}

.add-box-shadow {
  box-shadow:
    0 4px 8px 0 rgba(0, 0, 0, 0.2),
    0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.remove-box-shadow {
  box-shadow: none;
}

.hidden {
  box-shadow: none;
  display: none;
}

.pointer-cursor {
  cursor: pointer;
}

.button-style-blue {
  align-items: center;
  background: var(--go-brand-tahoe-blue, #20367c);
  border-color: var(--wpforms-button-border-color);
  border-radius: var(--wpforms-button-border-radius);
  border-style: var(--wpforms-button-border-style);
  border-width: var(--wpforms-button-border-size);
  box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.25);
  color: #fff;
  cursor: pointer;
  display: grid;
  font-family: inherit;
  font-size: var(--wpforms-button-size-font-size);
  font-weight: 500;
  height: 2.6rem;
  line-height: 100%;
  padding: 0 var(--wpforms-button-size-padding-h);
  position: relative;
  text-align: center;
  text-decoration: none;
  transition: all 0.15s ease-in-out;
  width: -moz-fit-content;
  width: fit-content;
}
.button-style-blue:hover {
  background: #1a2b63;
  text-decoration: none !important;
}

#less_services_toggle {
  margin-block-start: 2rem;
}

header
  #ca_gov_sidebar
  div.ca-gov-services-container
  > ul
  > li#view_all_cagov_li {
  border: 1px solid var(--brand-primary, #20367c);
  border-radius: 0.3125rem;
  padding: 0.5rem 0.5rem 0.5rem 1rem;
  text-align: left;
}

/* End of Move to Global */
header {
  align-items: center;
  background-color: var(--grey-background, #eee);
  box-shadow:
    0 4px 8px 0 rgba(0, 0, 0, 0.2),
    0 6px 20px 0 rgba(0, 0, 0, 0.19);
  container-type: inline-size;
  container-name: header;
  display: flex;
  width: 100%;
  padding: 20px;
  position: relative;
}
header .logo {
  align-self: center;
  margin-right: 2rem;
}
header .logo figure,
header .logo img {
  height: 4vh;
  width: 14vw;
}
header.compacted {
  position: fixed;
}
header.compacted .mobile {
  display: block;
}
header.compacted .logo figure,
header.compacted .logo img {
  width: 14vw;
}
header .navigation {
  flex: auto;
}
header .navigation nav {
  display: flex;
}
header .navigation nav ul {
  display: flex;
  width: 100%;
}
header .navigation nav ul li {
  align-self: center;
  margin: 0;
  margin-inline-end: clamp(0.5rem, 3vw, 1.25rem);
  padding: 0;
  padding-right: 3rem;
}
header .navigation nav ul li a {
  color: var(--tahoe-blue, #20367c);
  font-size: 1.2rem;
  font-weight: 700;
  text-decoration: none;
}
header .navigation nav ul li a:hover {
  border-bottom: 3px solid currentcolor;
}
header .navigation nav ul li a.active {
  border-bottom: 3px solid #fec335;
}
header nav li.search {
  flex: 1;
  text-align: right;
}
header nav li.search .search-container input[type="submit"] {
  display: block;
}
header nav li.search .search-close-icon {
  background: transparent;
  border: none;
  display: none;
  position: relative;
  top: 33px;
}

header nav li.search #search-box.focus-search-box ~ .search-close-icon {
  display: block;
}

/* CSS specific to iOS devices */
@supports (-webkit-touch-callout: none) {
  header.mobile .navigation-mobile nav {
    height: 70vh !important;
  }
}

/* Prevent zoom-in on Safari when clicking in search box */
header .navigation-mobile #search-box.focus-search-box {
  font-size: 16px !important;
}

